
#skill {
/*height 400px*/
  background: #4d4d4d;
}
#skill .content {
  width: 80%;
  padding: 90px 0;
  margin: 0 10%;
}
#skill .content .skills {
  -webkit-column-count: 2;
     -moz-column-count: 2;
          column-count: 2;
  -webkit-column-gap: 100px;
     -moz-column-gap: 100px;
          column-gap: 100px;
  list-style: none;
}
#skill .content .skills li {
  padding-bottom: 20px;
}
#skill .content .skills li * {
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
#skill .content .skills li:hover p {
  color: #ec8087;
}
#skill .content .skills li:hover .score {
  fill: url("#pinkGradient");
}
#skill .content .skills .score-container {
  height: 10px;
  width: 200px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  background: #444;
  border-radius: 3px;
}
#skill .content .skills .score {
  height: 10px;
  fill: url("#blue");
  border-radius: 3px;
}
#skill .content .skills p {
  margin: 0;
  display: inline-block;
  width: 50%;
  color: #fff;
}
#skill .content .keys {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
#skill .content .keys li {
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  margin-right: 1em;
  margin-top: 0.8em;
  padding: 0.3em 1em;
  border-radius: 999em;
  background: #74d8a8;
}
#skill .content .keys li:hover {
  background: #eff27c;
}


