
#header {
  height: 470px;
}
#header .background {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 800px;
}
#header .header-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 100px;
}
#header .header-content .icon {
  height: 35px;
  margin-bottom: 10px;
}
#header .header-content h1 {
  font-size: 72px;
  margin: 0;
}
#header .header-content h3 {
  font-size: 28px;
  font-weight: 200;
  margin: 0;
}
#header .header-content p {
  color: #1eb8d9;
  font-weight: 100;
  font-family: "Noto Sans S Chinese Thin";
  font-size: 22px;
  margin: 0;
}


