
.projects .content {
  background: #efefef;
  height: 700px;
  width: 100%;
}
.projects .go {
  position: absolute;
  z-index: 999;
  height: 300px;
  width: 18%;
  text-align: center;
  margin-top: 180px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  border-radius: 15px;
}
.projects .go i {
  margin: 108px auto;
  font-size: 84px;
  line-height: 1em;
  display: block;
}
.projects .go:hover {
  color: #e465a5;
}
.projects .go-left {
  left: 0;
}
.projects .go-right {
  right: 0;
}
.projects .viewer {
  width: 100%;
  margin: auto;
  overflow: hidden;
  padding: 100px 0;
  position: relative;
}
.projects .scrollpad {
  list-style: none;
  padding: 0;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
.projects .scrollpad li {
  display: inline-block;
  width: 100vw;
}
.projects .scrollpad .thumbnail {
  display: block;
  max-width: 60vw;
  margin: 0 40% 0 20%;
  box-shadow: 0 0 15px #ddd;
}
.projects .scrollpad li > h4 {
  text-align: center;
  width: 100%;
  margin: 0 auto;
  font-size: 32px;
}
.projects .scrollpad li > p {
  text-align: center;
  width: 100%;
  margin: 0 auto;
}
.projects .description {
  position: absolute;
  background: rgba(0,0,0,0.6);
  font-size: 20px;
  color: #fff;
  z-index: 99;
  width: 60vw;
  height: 400px;
  margin: 0 40% 0 20vw;
  opacity: 0;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  overflow: hidden;
  cursor: pointer;
}
.projects .description .container {
  margin: 30px;
  font-weight: 200;
  text-align: left;
}
.projects .description .container h4 {
  font-size: 20px;
  font-weight: 400;
  margin: 0;
  margin-top: 20px;
  cursor: pointer;
}
.projects .description .container p {
  font-size: 18px;
  margin: 0;
  cursor: pointer;
}
.projects .description:hover {
  opacity: 1;
}
.projects .banner {
  max-height: 40px;
  margin-top: 20px;
}
.projects .middle-banner {
  width: 100%;
}
.projects .start-banner {
  padding-left: 48.5%;
  width: 51.5%;
}
.projects .end-banner {
  padding-right: 48.5%;
  width: 51.5%;
}


