
#edu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 30px auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#edu * {
  -webkit-transition-duration: 500ms;
          transition-duration: 500ms;
}
.edu-block {
  color: #fff;
  width: 260px;
  height: 240px;
  margin: 0;
  background: #ddd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  overflow: hidden;
}
.edu-block div {
  margin-left: 24px;
}
.edu-block .upper {
  margin-top: 14px;
}
.edu-block .down {
  margin-bottom: 20px;
}
.edu-block p {
  width: 90%;
  margin: 5px;
  font-size: 20px;
  line-height: 1em;
}
.edu-block .large-label {
  vertical-align: text-top;
  text-indent: -4px;
  font-size: 48px;
  font-weight: 600;
  margin-bottom: 0.3em;
}
.edu-block:hover {
  box-shadow: 0 0 15px #999;
}
.edu-school {
  background: url("./background.svg") no-repeat;
  background-size: cover;
  color: #000;
}
.edu-school .upper {
  opacity: 0;
}
.edu-school .down {
  margin-bottom: 80px;
  margin-left: 24px;
}
.edu-school .down .large-label {
  font-size: 48px;
}
.edu-school:hover .upper {
  opacity: 100;
}
.edu-school:hover .down {
  margin-left: 24px;
  margin-bottom: 24px;
}
.edu-school:hover .down .large-label {
  font-size: 36px;
}
.edu-left {
  background: url("left.svg");
  background-size: cover;
}
.edu-left:hover p {
  color: #444;
}
.edu-right {
  text-align: end;
  background: url("right.svg");
  background-size: cover;
}
.edu-right .content {
  margin-top: 20px;
}
.edu-right .main-award {
  font-size: 24px;
  font-weight: 600;
}
.edu-right:hover .main-award {
  color: #ed8383;
}


