
@import url("//fonts.googleapis.com/earlyaccess/notosanssc.css");
body {
  margin: 0;
  font-family: "Noto Sans SC", sans-serif;
}
.container {
  margin: 20px 10%;
}
.lang-opt {
  position: fixed;
  z-index: 99;
  right: 30px;
  top: 16px;
}
.lang-opt [type=radio] {
  display: none;
}
.lang-opt input[type=radio]:checked + label {
  background: #34c1d9;
}
.lang-opt label {
  background: rgba(192,240,235,0.2);
  color: #fff;
  border-radius: 5px;
  padding: 0.1em 0.4em;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
.lang-opt label:hover {
  background: #34c1d9;
}


