
.footer {
  width: 100%;
  height: 450px;
  margin-bottom: -100px;
  background: #fff;
  bottom: 0;
  position: absolute;
  z-index: -1;
}
.info {
  position: relative;
  height: 300px;
}
.info .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 60%;
  margin: auto;
  padding-top: 150px;
  z-index: 999;
}
.info .content div {
  text-align: center;
  font-size: 16px;
}
.info .content div a {
  text-decoration: none;
  color: #2ba0b4;
}
.info .content div .iconfont {
  font-size: 56px;
  display: block;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
.info .content div:hover .iconfont {
  font-size: 92px;
}


